@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:400,500);
body{background-color:#f6f6f6;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", 'Roboto Mono', Helvetica, Arial, sans-serif}

.feed{min-height:500px;display:flex;flex-direction:column;width:100%;max-width:1200px;margin:30px auto;align-items:center}

.container .spinner{height:50px;-webkit-animation:spinner 2s steps(10) infinite;animation:spinner 2s steps(10) infinite}@-webkit-keyframes spinner{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spinner{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.feed-photo{border:1px solid #e6e6e6;background-color:white;border-radius:5px;width:100%;max-width:600px;margin-bottom:60px}.feed-photo:last-child{margin-bottom:0}.feed-photo .header{padding:15px;display:flex;align-items:center;border-bottom:1px solid #e6e6e6}.feed-photo .header .image{width:30px;border-radius:50%;margin-right:15px}.feed-photo .header .header-column{display:flex;flex-direction:column}.feed-photo .header .header-column .creator{font-weight:600;margin-bottom:5px}.feed-photo .header .header-column .location{font-size:12px}.feed-photo .meta{padding:15px;padding-bottom:0;padding-top:10px}





.overlay{position:fixed;top:0;left:0;background-color:rgba(0,0,0,0.5);width:100%;height:100%;display:flex;justify-content:center;align-items:center}.overlay .box{border:1px solid #e6e6e6;background-color:white;width:100%;max-width:600px}.overlay .box .header{display:flex;padding:14px 0;justify-content:center;align-items:center;border-bottom:2px solid #efefef}.overlay .box .header svg{cursor:pointer}.overlay .box .header .title{order:1;margin-left:auto;margin-right:auto;font-weight:600;font-size:15px}.overlay .box .content{min-height:300px;display:flex;justify-content:center;align-items:center}

.horizontal,.vertical{display:flex;justify-content:space-between;align-items:center;padding:10px 16px;width:100%;box-sizing:border-box;border-bottom:1px solid #efefef}.horizontal .column,.vertical .column{display:flex;align-items:center}.horizontal .column .user,.vertical .column .user{margin-left:10px}.horizontal .column .user .username,.vertical .column .user .username{font-weight:600;display:block;margin-bottom:3px}.horizontal .column .user .name,.vertical .column .user .name{color:#393e46}.horizontal .column .bigAvatar,.vertical .column .bigAvatar{width:55px;border-radius:50%}.horizontal .column .avatar,.vertical .column .avatar{width:30px;border-radius:50%}.horizontal .column .button,.vertical .column .button{-webkit-appearance:none;border-radius:3px;background-color:#3897f0;color:white;font-weight:600;border:0;font-size:16px;line-height:26px;cursor:pointer;width:100%;padding:2px 9px}.horizontal .column .button:focus,.horizontal .column .button:active,.vertical .column .button:focus,.vertical .column .button:active{outline:none}.horizontal .column .button:active,.vertical .column .button:active{opacity:0.7}.horizontal:last-child{border:0}.vertical{flex-direction:column;width:30%;justify-content:center;align-items:center;height:220px;border:1px solid #e6e6e6;background-color:white;border-radius:5px}.vertical .column{flex-direction:column}.vertical .column .avatar{width:78px;margin-bottom:20px}.vertical .column .user{text-align:center;margin:0}.vertical .column .user .username{margin-bottom:7px}.vertical .button{margin-top:15px;min-width:95px}

.footer{max-width:1200px;width:100%;margin:0 auto;display:flex;align-items:center;justify-content:center;font-weight:600;text-transform:uppercase;font-size:12px;margin-top:55px;margin-bottom:55px;flex-wrap:wrap}@media (max-width: 37.5em){.footer{justify-content:center}}.footer .column{padding:0 50px;margin-bottom:15px}@media (max-width: 37.5em){.footer .column{margin-bottom:0}}.footer .column .nav .list{display:flex;flex-wrap:wrap}@media (max-width: 37.5em){.footer .column .nav .list{justify-content:center}}.footer .column .nav .list .listitem{margin-left:20px;color:#222831;cursor:pointer}@media (max-width: 37.5em){.footer .column .nav .list .listitem{margin-bottom:15px}}.footer .column .copyright{color:#393e46}

.auth{width:100%;max-width:100%;margin:0 auto;display:flex;min-height:100%;margin-top:30px;justify-content:center;align-items:center}.auth .column{width:40%}.auth .column:last-child{width:35%;margin-left:35px}.auth .column .app-box{display:flex;flex-direction:column;align-items:center;margin-top:35px}.auth .column .app-box .appstores{margin-top:25px}.auth .column .app-box .appstores img{height:40px}.auth .column .app-box .appstores img:first-child{margin-right:10px}.auth .column .white-box{text-align:center;padding:15px;border:1px solid #e6e6e6;background-color:white}.auth .column .white-box .change-link{color:#3897f0;cursor:pointer}.auth .column .white-box p{margin:15px}.auth .column .form-box{margin-bottom:10px}

.form-component{display:flex;flex-direction:column}.form-component .form{display:flex;flex-direction:column}.form-component .form .text-input{border:1px solid #efefef;border-radius:3px}.form-component .divider{text-transform:uppercase;color:#393e46;font-weight:600;margin:20px 0;margin-bottom:20px;position:relative}.form-component .divider:before{content:"";width:42%;position:absolute;background-color:#393e46;height:1px;left:0;top:6px;opacity:0.8}.form-component .divider:after{content:"";width:42%;position:absolute;background-color:#393e46;height:1px;right:0;top:6px;opacity:0.8}.form-component .facebook-link{align-items:center;color:#385185;justify-content:center;margin-bottom:25px;cursor:pointer;background-color:white;border:0;font-size:16px;text-align:center}.form-component .facebook-link i{margin-right:5px}.form-component .facebook-link:focus,.form-component .facebook-link:active{outline:none}

.navigation{width:100%;border:1px solid #e6e6e6;background-color:white;display:flex;justify-content:center}.navigation .inner{max-width:1200px;display:flex;justify-content:space-between;align-items:center;width:100%;height:77px;padding:25px 0}.navigation .inner .column{display:flex;justify-content:center;align-items:center;width:33%}.navigation .inner .column:first-child{justify-content:flex-start}.navigation .inner .column:last-child{justify-content:flex-end}.navigation .inner .column .logo{max-width:125px}.navigation .inner .column .search-input{width:215px;box-sizing:border-box;padding:5px;border:solid 1px #dbdbdb;border-radius:3px;background-color:#fafafa;font-size:16px;font-weight:300}.navigation .inner .column .search-input::-webkit-input-placeholder{color:#393e46}.navigation .inner .column .search-input::-moz-placeholder{color:#393e46}.navigation .inner .column .search-input:-ms-input-placeholder{color:#393e46}.navigation .inner .column .search-input::-ms-input-placeholder{color:#393e46}.navigation .inner .column .search-input::placeholder{color:#393e46}.navigation .inner .column .search-input:focus{outline:none;background-color:white}.navigation .inner .column .nav-icon{margin-left:30px;cursor:pointer}

.explore{min-height:500px;display:flex;flex-direction:column;width:100%;max-width:1200px;margin:0 auto;align-items:center}

.search{display:flex;flex-direction:column;width:100%;max-width:1200px;margin:0 auto;align-items:center}.search .section{width:100%;margin-bottom:80px}.search .section .title{text-align:left;color:#393e46;font-weight:600;margin-bottom:20px}.search .section .not-found{text-align:center;width:100%;display:block;font-weight:600}

.container{width:30%;height:220px;margin-bottom:50px;overflow:hidden;position:relative}.container .photo{min-height:100%;max-width:100%}.container:hover .overlay{opacity:1}.container .overlay{top:0;left:0;height:100%;width:100%;background-color:rgba(0,0,0,0.5);position:absolute;display:flex;justify-content:center;align-items:center;opacity:0}.container .overlay .data{color:white;font-weight:600;display:flex;align-items:center}.container .overlay .data:first-child{margin-right:10px}.container .overlay .data svg{margin-right:5px}

body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto Mono', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

